.nab-google-analytics-tracking-setting {
	margin-top: 0.6em;

	&--details {
		margin-top: 1em;

		.components-base-control__label {
			display: block;
		}

		.components-radio-control {
			margin-bottom: 2em;

			&__option-description {
				font-size: 0.9em;
			}
		}
	}

	&--secret .components-text-control__input {
		-webkit-text-security: disc;
	}
}
